import Vue from 'vue'
import Router from 'vue-router'
import joinMe from '@/components/joinMe.vue'
import index from '@/components/index.vue'
import myCenter from '@/components/myCenter.vue'
import repairs from '@/components/repairs.vue'
import bmap from '@/components/bmap.vue'
import query from '@/components/query.vue'
import login from '@/components/login.vue'
import question from '@/components/question.vue'
import reg from '@/components/reg.vue'
import updateInfo from '@/components/updateInfo.vue'
import forgetUpwd from '@/components/forgetUpwd.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'index',
      component:index,
      title:'在线保修'
    },
    {
      path: '/joinMe',
      name: 'joinMe',
      component: joinMe,
      title:'加入我们'
    },
    {
      path:'/myCenter',
      name:'myCenter',
      component:myCenter,
      title:'个人中心'
    },
    {
      path:'/repairs',
      name:'repairs',
      component:repairs,
      title:'立即报修'

    },
    {
      path:'/bmap',
      name:'bmap',
      component:bmap,
    },
    {
      path:'/query',
      name:'query',
      component:query,
      title:'保修查询'
    },
    {
      path:'/login',
      name:'login',
      component:login,
      title:'登录'
    },
    {
      path:'/question',
      name:'question',
      component:question,
      title:'问题反馈'
    },
    {
      path:'/reg',
      name:'reg',
      component:reg,
      title:'成为会员'
    },
    {
      path:'/updateInfo',
      name:'updateInfo',
      component:updateInfo,
      title:'更新信息'
    },
    {
      path:'/forgetUpwd',
      name:'forgetUpwd',
      component:forgetUpwd,
      title:'忘记密码'
    }
  ]
})
