<template>
  <div class="overlayer" @touchmove.prevent>
    <div class="bgimg">
      <img src="../assets/bg.png" alt />
    </div>
    <div class="queryAll">
      <div class="title">
        <span>全部订单</span>
      </div>
      <div v-for="(item) in list" :key="item" class="query">
        <div class="ddNum">
          <div class="ddNump">
            <p>订单编号：</p>
            <p>1234567</p>
          </div>
          <button>取消订单</button>
        </div>
        <div class="content1">
          <div class="content1Left">
            <img v-image-preview src="../assets/tx.png" />
          </div>
          <div class="content1Right">
            <p class="content1Detail">故障描述，故障描述,故障描述，故障描述</p>
            <div class="content1Yuyin">语音</div>
          </div>
        </div>
        <div class="Instr">
          <div class="serviceman">
            <img src="../assets/ren.png" alt />
            <p>维修人员：</p>
            <p style="margin-right:40%">测试</p>
            <p>已接受</p>
          </div>
          <div class="telephone">
            <img src="../assets/phone.png" alt />
            <p>联系电话:</p>
            <p>123456789</p>
          </div>
          <div class="appointmentTime">
            <img src="../assets/time.png" alt />
            <p>预约时间:</p>
            <p>2020年3月26日 上午11点</p>
          </div>
        </div>
      </div>
      <div class="quickly">
        <button>我要催单</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [1, 2]
    };
  },
  components: {}
};
</script>

<style>
/* 图片点击放大 */
.vue-directive-image-previewer {
  z-index: 999;
}
</style>

<style scoped>
/*遮罩层*/
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.bgimg {
  z-index: -1;
  position: absolute;
  /* top: 50px; */
  left: -300px;
}
.bgimg>img{
  width: 1600px;
  height: 1600px;
}
.queryAll {
  height: 100%;
}
.title {
  width: 100%;
  height: 5%;
  background: #efefef;
  padding: 2% 0 1% 2%;
}
.title > span {
  width: 20%;
  display: block;
  color: #ffaf00;
}
.ddNum,
.ddNump {
  display: flex;
}
.ddNum > button {
  width: 25%;
  border-radius: 20px;
  border: 1px solid #ffaf00;
  background: #fff;
  color: #ffaf00;
  outline: none;
}
.ddNum {
  display: flex;
  justify-content: space-between;
  padding: 2% 2% 2% 3%;
}
.ddNump > p {
  display: block;
  color: #2279b0;
}
.ddNump :first-child {
  text-align: right;
}
.ddNump :nth-child(2) {
  text-align: left;
}
.content1 {
  height: 14%;
  display: flex;
  justify-content: space-between;
  background: #eff3f2;
  border-radius: 15px;
  padding: 3% 0 2% 0;
  margin-bottom: 2%;
}
.query {
  margin-top: 2%;
}
.content1Left {
  width: 30%;
  height: 100%;
}
.content1Left > img {
  width: 80%;
  height: 80%;
}
.content1Right {
  width: 70%;
}
.content1Detail {
  width: 90%;
  margin: 0 auto;
  color: #3498db;
  text-align: left;
}
.Instr {
  border-bottom: 1px solid rgb(150, 158, 150);
  padding: 2% 2% 2% 3%;
}
.serviceman,
.telephone,
.appointmentTime {
  display: flex;
}
.serviceman > img,
.telephone > img,
.appointmentTime > img {
  width: 18px;
  height: 18px;
}
.serviceman p:first-child,
.telephone p:first-child,
.appointmentTime p:first-child {
  width: 25%;
}
.quickly {
  margin-top: 5%;
}
.quickly > button {
  width: 40%;
  height: 30px;
  margin: 0 auto;
  background: #ffaf00;
  border: transparent;
  color: #fff;
  border-radius: 20px;
  outline: none;
}
</style>


