<template>
    <div class="overlayer" @touchmove.prevent>
      <div class="bgimg">
        <img src="../assets/bg.png" class="bgimg" alt />
      </div>

      <div class="updateALL">
        <!-- 上传头像 -->
        <div class="updateTx">
          <img src="../assets/tx.png" alt="">
        </div>

        <div class="password">
          <img src="../assets/upwd.png" alt="">
          <input type="password " v-model="uname" placeholder="请输入密码（长度在6-12为之间）">
        </div>
        <div class="password2">
          <img src="../assets/email.png" alt="">
          <input type="password" v-model="upwd" placeholder="请输入电子邮件">
        </div>
        <div class="phone">
          <img src="../assets/tel.png" alt="">
          <input type="text" v-model="upwd" placeholder="请输入正确的手机号">
        </div>
        <div class="name">
          <img src="../assets/uname.jpg" alt="">
          <input type="text " v-model="uname" placeholder="请输入昵称">
        </div>

        <button @click="done()" class="done">登录</button>
      </div>      
    </div>
</template>

<script>
export default {
  methods:{
    done(){
      this.$router.push('/myCenter')
    }
  }
}
</script>

<style scoped>
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 90%;
  z-index: 10;
}
.bgimg{
  z-index: -1;
  position: absolute;
  left: -100%;
}
.bgimg>img{
  width: 2000px;
  height: 1600px;
}


.updateALL{
  width: 90%;
  height: 100%;
  margin: 0 auto;
}

.updateTx>img{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-top: 20%;
}
.password{
  /* margin-top: 20%; */
}
.password,.password2,.phone,.name{
  border-bottom: 1.5px solid #ccc;
  height: 12%;
  /* display: flex;
  justify-content: space-between; */
  position: relative;
}
.password>input,.password2>input,.phone>input,.name>input{
  display:block;
  width: 50%;
  position: absolute;
  bottom: 15%;
  left: 35%;
  background: transparent;
  border: transparent;
  outline: none;
  font-size: 16px;
}
.password>img,.password2>img,.phone>img,.name>img{
  display: block;
  width: 24px;
  height: 24px;
  position:absolute;
  bottom:15%;
  left:15%
}
.password>input::placeholder,.password2>input::placeholder,.phone>input::placeholder,.name>input::placeholder{
  font-size: 14px;
}

.done{
  width: 90%;
  height: 6%;
  background: #3498DB;
  outline: none;
  border: 1px solid #fff;
  margin-top: 34%;
}

</style>


