<template>
  <div class="center" :style="htmlheight">
    <div class="overlayer" @touchmove.prevent>
    <div class="center-title">
      <div class="tx">
        <img v-image-preview src="../assets/tx.png" />
      </div>
      <p style="color:rgb(27, 74, 119);">用户名</p>
    </div>
      <div class="bgimg">
        <img src="../assets/bg.png" class="bgimg" alt />
      </div>
    <div class="content">
      <div class="vip" @click="login">
        <div class="imgp">
          <img src="../assets/center.png" alt />
          <p>成为会员</p>
        </div>
        <p>></p>
      </div>
      <div class="question">
        <div class="imgp">
          <img src="../assets/question.png" alt />
          <p>常见问题</p>
        </div>
        <p>></p>
      </div>
      <div class="about">
        <div class="imgp">
          <img src="../assets/about.png" alt />
          <p>关于我们</p>
        </div>
        <p>></p>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      htmlheight: 0
    };
  },
  mounted() {
    const htmlh = window.screen.height;
    this.htmlheight = "height:" + window.screen.height + "px";
    // console.log(window.screen.height )
  },
  methods:{
    login(){
      this.$router.push("/login");
    }
  }
};
</script>

<style scoped>
/*遮罩层*/
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 90%;
  z-index: 10;
}
.bgimg{
  z-index: -1;
  position: absolute;
}
.bgimg>img{
  width: 1600px;
  height: 1600px;
}
.content{
  z-index: 99;
}
.center-title {
  width: 100%;
  height: 33%;
  background: #d6ece5;
}
.tx > img {
  margin-top: 8%;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}
.vip,.question,.about {
  width: 90%;
  height: 33px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #c9c9c9;
  margin-top: 7%;
}
.imgp > img {
  width: 24px;
  height: 24px;
  margin-right: 5%;
}
.imgp {
  width: 35%;
  display: flex;
}
</style>


