<template>
  <div class="overlayer" @touchmove.prevent>
    <div class="bgimg">
      <img src="../assets/bg.png" class="bgimg" alt />
    </div>
    <div class="loginALL">
      <div class="username">
        <img src="../assets/uname.jpg" alt="">
        <input type="text " v-model="uname" placeholder="请输入用户名">
      </div>
      <div class="password">
        <img src="../assets/upwd.png" alt="">
        <input type="password" v-model="upwd" placeholder="请输入密码">
      </div>
      <p class="forgetUpwd" @click="forget()">忘记密码？</p>
      <button @click="log()" class="log">登录</button>
      <button @click="reg()" class="reg">注册</button>

      <form class="solid">
        <fieldset class="fie">
          <legend>&nbsp;或用其他账户登录&nbsp;</legend>
        </fieldset>
      </form>

      <div class="wechat">
        <img src="../assets/wx.jpg" alt="">
        <p>微信</p>
      </div>
    </div> 
  </div>
</template>

<script>
export default {
  data() {
    return {
      user:'',
      upwd:''
    };
  },
  methods: {
    log() {
      var url = `user/login.do?user=${this.user}&&upwd=${this.upwd}`;
      this.$axios.post(url).then(res => {
        console.log(res.data.data);
      });
    },
    reg(){
      this.$router.push('/reg')
    },
    forget(){
      this.$router.push('/forgetUpwd')
    }
  }
};
</script>

<style scoped>
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 90%;
  z-index: 10;
}
.bgimg{
  z-index: -1;
  position: absolute;
  left: -140%;
}
.bgimg>img{
  width: 2500px;
  height: 1600px;
}

.loginALL{
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
.username{
  margin-top: 15%;
}
.username,.password{
  border-bottom: 1.5px solid #ccc;
  height: 12%;
  /* display: flex;
  justify-content: space-between; */
  position: relative;
}
.username>input,.password>input{
  display:block;
  width: 50%;
  position: absolute;
  bottom: 15%;
  left: 35%;
  background: transparent;
  border: transparent;
  outline: none;
  font-size: 16px;
}
.username>img,.password>img{
  display: block;
  width: 24px;
  height: 24px;
  position:absolute;
  bottom:15%;
  left:15%
}
.username>input::placeholder,.password>input::placeholder{
  font-size: 14px;
}
.forgetUpwd{
  width: 100px;
  height: 23px;
  color: #0046ff;
  border-bottom: 1px solid #0046ff;
  display: block;
  text-align: center;
  float:right;
  margin-top: 20%;
}
.log,.reg{
  width: 90%;
  height: 6%;
  background: #3498DB;
  outline: none;
  border: 1px solid #fff;
  margin-top: 10%;
}
.reg{
  background: #fff;
  border: 1px solid #3498DB;
  margin-top: 5%;
}
.solid{
  width: 90%;
  margin: 0 auto;
  margin-top: 20%;
  color: #96a6a6;
}
.fie{
  border-bottom: transparent;
  border-left: transparent;
  border-right: transparent;
  border-top: 1px solid #ccc;
}
.wechat{
  margin-top: 5%;
}
</style>


