<template>
  <div class="overlayer" @touchmove.prevent @click="do1">
    <div class="bgimg">
      <img src="../assets/bg.png" alt />
    </div>
    <div class="bxDetails">
      <mt-field class="field" label="报修人姓名:" placeholder="请输入姓名" v-model="username"></mt-field>
      <div class="block">
        <span class="demonstration" style="width: 100px; display: block; height: 20px;">预约时间</span>
        <el-date-picker v-model="value1" type="datetime" placeholder="选择日期时间"></el-date-picker>
      </div>
      <mt-field class="field" label="报修人电话：" placeholder="请输入电话" type="tel" v-model="phone"></mt-field>
      <div class="field">
        <span style="width: 100px;display: block;height: 20px;">报修人地址:</span>
        <input
          readonly="readonly"
          placeholder="北京市海淀区莲花池西路16号金鑫大厦
"
          style="border:transparent;outline:none;width:80%;position:absolute;top:50%;    background: transparent;"
        />
      </div>
      <!-- 选择地图 -->
      <div class="map">
        <div class="weizhi">
          <img src="../assets/weizhi.png" alt />
          <p>选择地址</p>
        </div>
      </div>
      <!--  -->
    </div>
    <!-- 故障描述 -->
    <div class="repairDetail">
      <p style="color:#3498db;font-weight:700;width:25%;display: block;text-align:left">故障描述</p>
      <textarea class="upload-font" cols="30" rows="1" placeholder="输入问题描述,上传语音或图片"></textarea>
      <!-- 上传语音 -->
      <div class="yx">
        <button class="yuyin">上传语音</button>
      </div>
      <!-- 上传图片 -->
      <div class="uploadImg">
        <el-upload
          action="#"
          list-type="picture-card"
          :auto-upload="false"
          :limit="3"
          accept=".jpg, .jpeg, .png, .gif, .bmp, .pdf, .JPG, .JPEG, .PBG, .GIF, .BMP, .PDF"
         >
          <i slot="default" class="el-icon-plus" @click="handleExceed()"></i>
          <div slot="file" slot-scope="{file}">
            <img class="el-upload-list__item-thumbnail" :src="file.url" alt />
            <span class="el-upload-list__item-actions">
              <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
                <i class="el-icon-zoom-in"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="handleDownload(file)"
              >
                <i class="el-icon-download"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="handleRemove(file)"
              >
                <i class="el-icon-delete"></i>
              </span>
            </span>
          </div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </div>
    </div>
    <div class="tijiao">
      <button>提交</button>
    </div>
  </div>
</template>

<script>
export default {
  computed:{
    getchild(){
      return document.getElementsByClassName('el-upload-list el-upload-list--picture-card')[0].children.length
    }
  },
  watch:{
    getchild(){
      this.checkchild()
    }
    
  },
  mounted(){

  },
  data() {
    return {
      picnum:0,
      username: "",
      birthday: "",
      phone: "",
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      pickerOptions: {
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      value1: "",
      value2: "",
      value3: ""
    };
  },
  methods: {
    do1(){
      // console.log(this.arr1)
    },
    handleExceed(){
      console.log(1)
      const inputchild=document.getElementsByClassName('el-upload-list el-upload-list--picture-card')[0].children.length
      this.picnum=inputchild
      const inputadd=document.getElementsByClassName('el-upload el-upload--picture-card')
      if(inputchild==2){
        inputadd[0].style.display='none'
      }
    },
    user() {
      if (username == "") {
      }
    },
    handleRemove(file) {
      console.log(file);
    },
    // handlePictureCardPreview(file) {
    //   this.dialogImageUrl = file.url;
    //   this.dialogVisible = true;
    // },
    handleDownload(file) {
      console.log(file);
    }
  }
};
</script>

<style lang="">
.mint-cell-wrapper {
  background-size: 120% 0px !important;
}
.mint-cell-wrapper {
  padding: 0 !important;
}
.el-upload-list__item-actions {
  width: 50px !important;
  height: 50px !important;
}
.el-upload--picture-card{
  width: 100px;
  height: 100px;
}
.el-upload-list--picture-card .el-upload-list__item{
  width: 100px;
  height: 100px;
}
.el-picker-panel el-date-picker el-popper has-time {
  width: 65% !important;
}
.el-picker-panel__content {
  width: 90% !important;
}
.el-time-panel el-popper {
  width: 103% !important;
}
.mint-cell {
  background-color: transparent !important;
}
.mint-field-core,
.el-input__inner {
  background: transparent;
}
</style>
<style scoped>
* {
  box-sizing: border-box;
}
/*遮罩层*/
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.bgimg {
  z-index: -1;
  position: absolute;
  left: -150px;
  top: -150px;
}
.bgimg>img{
  width: 1600px;
  height: 1600px;
}
.bxDetails,
.repairDetail,
.tijiao {
  z-index: 10;
}
.block,
.field {
  height: 60px;
  padding: 0 10px;
  margin-top: 2%;
  display: flex;
  position: relative;
  border-bottom: 0px;
  width: 90%;
  margin: 0 auto;
  text-align: left;
  border-bottom: 1px solid #dfdfdf;
  display: flex;
}
.block {
  line-height: 60px;
}
.map {
  width: 100%;
  height: 40px;
  background: #eff2fd;
}
.weizhi {
  display: flex;
  width: 30%;
  float: right;
  margin-top: 1.5%;
}
.weizhi > p {
  color: #3498db;
}
.weizhi > img {
  width: 24px;
  height: 24px;
}
.repairDetail {
  width: 90%;
  margin: 0 auto;
}
.upload-font {
  width: 100%;
  height: 60px;
  background: #eff3f2;
  border-radius: 10px;
  padding: 2%;
  resize: none;
  border: transparent;
  outline: none;
  font-size: 16px;
}
.upload-font::placeholder {
  color: #3498db;
}
.yx {
  width: 30%;
}
.yuyin {
  width: 100%;
  height: 30px;
  background-color: #ffaf00;
  border: transparent;
  color: #fff;
  border-radius: 3px;
  outline: none;
  margin-bottom: 5%;
}
.uploadImg {
  margin-bottom: 3%;
}
.tijiao > button {
  width: 45%;
  height: 35px;
  background: #30c9f7;
  border: transparent;
  color: white;
  outline: none;
  border-radius: 3px;
}
</style>


