<template>
  <div class="overlayer" @touchmove.prevent>
    <div class="bgimg">
      <img src="../assets/bg.png" alt />
    </div>
    <div id="all">
      <div id="input">
        <div id="username">
          <p>
            <span class="span-font">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名：</span>
          </p>
          <input type="text" class="input-size" placeholder="bjmlby@163.com" />
        </div>
        <div class="sex">
          <p>
            <span class="span-font">性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别：</span>
          </p>
          <select class="input-sex" v-model="selectCurriculums">
            <option v-for="(curriculum, index) in curriculums" :key="index">{{curriculum}}</option>
          </select>
        </div>
        <div id="danwei">
          <p>
            <span class="span-font">单&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位：</span>
          </p>
          <input type="text" class="input-size" placeholder="北京茂澜宝业" />
        </div>
        <div id="phone">
          <p>
            <span class="span-font">手&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机：</span>
          </p>
          <input type="text" class="input-size" placeholder="18801230123
" />
        </div>
        <div id="zhuzhi">
          <p>
            <span class="span-font">住&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址：</span>
          </p>
          <input type="text" class="input-size" placeholder="北京市海淀区" />
        </div>
        <div id="louhao">
          <p>
            <span class="span-font">楼&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：</span>
          </p>
          <input type="text" class="input-size" placeholder="18" />
        </div>
        <div id="danyuanhao">
          <p>
            <span class="span-font">单&nbsp;元&nbsp;&nbsp;号：</span>
          </p>
          <input type="text" class="input-size" placeholder="2单元" />
        </div>
        <div id="menpaihao">
          <p>
            <span class="span-font">门&nbsp;牌&nbsp;&nbsp;号：</span>
          </p>
          <input type="text" class="input-size" placeholder="301" />
        </div>
        <div id="email">
          <p>
            <span class="span-font">电子邮件：</span>
          </p>
          <input type="text" class="input-size" placeholder="1282018981@qq.com" />
        </div>
        <!-- 提交按钮 -->
        <div class="Button">
          <button @click="jumpLogin()">加入我们</button>
          <!-- onclick="javascript:window.location.href='https://www.baidu.com'" -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selectCurriculums: "男",
      curriculums: ["男", "女"]
    };
  },
  methods: {
    jumpLogin() {
      // this.$router.push('https://www.baidu.com')
    }
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  outline: none;
}
/*遮罩层*/
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.bgimg {
  z-index: -1;
  position: absolute;
  left: -150px;
  top: -150px;
}
.bgimg>img{
  width: 1600px;
  height: 1600px;
}


/* ******** */
#all {
  width: 90%;
  margin: 0 auto;
}
#input {
  width: 100%;
  /* position: absolute;
			top: 50%; */
  /* transform: translateY(6%) */
}
#input > div > p {
  display: inline-block;
}
#input > div {
  margin-bottom: 6%;
}
#username {
  margin-top: 15%;
}
.span-font {
  font-size: 14px;
}
.input-size {
  width: 70%;
  height: 30px;
  border-radius: 7px;
  border: 1px solid #fff;
  background: #eff3f2;
  padding-left: 3px !important;
}
.input-sex {
  width: 70%;
  height: 32px;
  border-radius: 7px;
  border: 1px solid #fff;
  background: #eff3f2;
  padding-left: 3px;
  box-sizing: border-box;
}
.Button > button {
  width: 80%;
  height: 40px;
  border-radius: 30px;
  border: 1px solid #333;
  background: #1bbc9d;
  position: absolute;
  bottom: 1%;
  left: 11%;
}
</style>


