<template>
    <div class="overlayer" @touchmove.prevent>
      <div class="bgimg">
        <img src="../assets/bg.png" class="bgimg" alt />
      </div>
      <div class="regALL">
        <div class="username">
          <img src="../assets/uname.jpg" alt="">
          <input type="text " v-model="uname" placeholder="请输入用户名">
        </div>
        <div class="password">
          <img src="../assets/upwd.png" alt="">
          <input type="password" v-model="upwd" placeholder="请输入密码">
        </div>
        <div class="password2">
          <img src="../assets/sure.png" alt="">
          <input type="text " v-model="uname" placeholder="请输入用户名">
        </div>
        <div class="phone">
          <img src="../assets/tel.png" alt="">
          <input type="password" v-model="upwd" placeholder="请输入密码">
        </div>

        <button @click="log()" class="log">登录</button>
      </div>
    </div>
</template>

<script>
export default {
  methods:{
    log(){
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped>
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 90%;
  z-index: 10;
}
.bgimg{
  z-index: -1;
  position: absolute;
  left: -100%;
}
.bgimg>img{
  width: 2100px;
  height: 1600px;
}

.regALL{
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
.username{
  margin-top: 35%;
}
.username,.password,.password2,.phone{
  border-bottom: 1.5px solid #ccc;
  height: 12%;
  /* display: flex;
  justify-content: space-between; */
  position: relative;
}
.regALL>div>input{
  display:block;
  width: 50%;
  position: absolute;
  bottom: 15%;
  left: 35%;
  background: transparent;
  border: transparent;
  outline: none;
  font-size: 16px;
}
.regALL>div>img{
  display: block;
  width: 24px;
  height: 24px;
  position:absolute;
  bottom:15%;
  left:15%
}

.regALL>div>input::placeholder{
  font-size: 14px;
}

.log{
  width: 90%;
  height: 6%;
  background: #3498DB;
  outline: none;
  border: 1px solid #fff;
  margin-top: 34%;
}
</style>


