<template>
  <div class="overlayer" @touchmove.prevent>
    <mt-tab-container v-model="active">
      <mt-tab-container-item id="tab1">
        <div class="index-all" :style="htmlheight">
          <div class="title-div">
            <!-- 轮播图 -->
            <div class="title-lbt">
              <mt-swipe :auto="6000"> 
                <mt-swipe-item>
                  <img src="../assets/logo.png" alt style="width:100%;height:100%" />
                </mt-swipe-item>
                <mt-swipe-item>
                  <img src="../assets/uo.png" alt style="width:100%;height:100%" />
                </mt-swipe-item>
              </mt-swipe>
            </div>
          </div>
          <div class="bgimg">
            <img src="../assets/bg.png" class="bgimg" alt />
          </div>
          <!-- 报修按钮 -->
          <div class="bx">
            <div class="ljbx" @click="repairs">
              <div class="bximgp">
                <img src="../assets/ljbx.png" alt />
                <p>立即报修</p>
              </div>
            </div>
            <div class="bxcx">
              <div class="bximgp" @click="query">
                <img src="../assets/bxcx.png" alt />
                <p>报修查询</p>
              </div>
            </div>
            <div class="sbxy">
              <div class="bximgp" @click="joinMe">
                <img src="../assets/sbxy.png" alt />
                <p>加入我们</p>
              </div>
            </div>
            <div class="xxfk">
              <div class="bximgp" @click="question">
                <img src="../assets/xxfk.png" alt />
                <p>问题反馈</p>
              </div>
            </div>
          </div>
          <!-- 通知栏 -->
          <div class="tongzhi">
            <img src="../assets/tongzhi.png" alt />
            <div
              style="border-right:1px solid #333;width: 3%;height: 20px;float: left;margin-top: 1.5%;"
            ></div>
          </div>
          <!-- 新闻列表 -->
          <div class="news">
            <div style="width:45%">
              <ul style="margin-left: 10%;text-align:left;">
                <li>新闻列表1新闻1</li>
                <li>新闻列表2新闻2</li>
                <li>新闻列表3新闻3</li>
                <li>新闻列表4新闻4</li>
                <li>新闻列表5新闻5</li>
              </ul>
            </div>
            <div style="width:45%">
              <ul style="margin-left: 10%;text-align:left;">
                <li>新闻列表1新闻1</li>
                <li>新闻列表2新闻2</li>
                <li>新闻列表3新闻3</li>
                <li>新闻列表4新闻4</li>
                <li>新闻列表5新闻5</li>
              </ul>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
      <!-- 面板 -->
      <mt-tab-container-item id="tab2">
        <!-- 我的页面 -->
      <!-- <div class="bgimg">
      <img src="../assets/bg.png" class="bgimg" alt />
    </div> -->
        <myCenter></myCenter>
      </mt-tab-container-item>
    </mt-tab-container>
    <!-- 底部 -->
    <mt-tabbar fixed v-model="active" style="z-index:99">
      <mt-tab-item id="tab1" class="index">
        <img slot="icon" src="../assets/shouye.png" />
        首页
      </mt-tab-item>
      <mt-tab-item id="tab2" class="me">
        <img slot="icon" src="../assets/me.png" />
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import myCenter from "./myCenter";
export default {
  data() {
    return {
      active: "tab1",
      htmlheight: 0
    };
  },
  methods: {
    repairs() {
      this.$router.push("/repairs");
    },
    query() {
      this.$router.push("/query");
    },
    joinMe(){
      this.$router.push("/joinMe");
    },
    question(){
      this.$router.push("/question");
    }
  },
  // created() {
  //   document.title = "在线报修";
  // },
  mounted() {
    const htmlh = window.screen.height;
    this.htmlheight = "height:" + window.screen.height + "px";
    console.log(htmlh);
  },
  components: {
    myCenter
  }
};
</script>

<style>
/* 图片点击放大 */
.vue-directive-image-previewer {
  z-index: 999;
}
</style>

<style scoped>
* {
  box-sizing: border-box;
}
/*遮罩层*/
.overlayer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.bgimg {
  z-index: -1;
  position: absolute;
}
.bgimg>img{
  width: 1600px;
  height: 1600px;
}

.tongzhi,
.news {
  z-index: 99;
  /* position: absolute; */
}
.title-div {
  width: 100%;
  height: 30%;
}
.title-lbt {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.title-lbt::after {
  display: block;
  content: "";
  clear: both;
}
.bx {
  width: 100%;
  height: 21%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 1%;
  background: #fff;
  /* border:2px solid #dfdfdf; */
}
.ljbx,
.bxcx,
.sbxy,
.xxfk {
  width: 50%;
  display: flex;
  border: 1px solid #dfdfdf;
}
.bximgp {
  height: 32px;
  display: flex;
  margin: 0 auto;
  margin-top: 9%;
}
.bximgp > img {
  width: 32px;
  height: 32px;
  margin-right: 5px;
}
.tongzhi {
  width: 100%;
  padding-left: 2%;
  border: 1px solid #dfdfdf;
  border-radius: 5px;
  margin-top: 7%;
  /* background: rgba(0, 0, 0, 0.1); */
  margin-bottom: 5%;
}
.tongzhi > img {
  float: left;
}
.tongzhi::after {
  display: block;
  content: "";
  clear: both;
}
.news {
  display: flex;
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
}
.news > ul {
  width: 50%;
}
.news > ul > li {
  width: 100%;
}
</style>


